@use '../inc/vars' as *;
.activity {
  &__bg {
    top: -250px;
    bottom: -250px;
    left: -250px;
    right: -250px;
    margin: auto;
    position: absolute;
    transform: scale(0);
    z-index: -1;
    transition: all 1s 0s;
    
    /* &.active-past {
      transition: all 0s 1s;
      transform: scale(1);
      z-index: 0;
    } */
    &.active {
      transform: scale(1);
      z-index: 0;
      transition: all 1s 0s;
    }
    content: '';
      border-radius: 1000px;
    &--0 {
      background-color: $green;
    }
    &--1 {
      background-color: $blue;
    }
    &--2 {
      background-color: $text-light;
    }
  }
  .section {
    &__header {
      @media(max-width: $xl){
      
      }
      @media(max-width: $lg){
      
      }
      @media(max-width: $md){
        align-items: flex-end !important;
        justify-content: flex-end !important;
      }
      @media(max-width: $sm){
      
      }
      @media(max-width: $xs){
      
      }
      
    }
    &__title {

    }
    &__body {
      &.pseudo {
        &::after {
          top: 0;
          bottom: 0;
          left: 0;
          right: 0;
          content: '';
          position: absolute;
          z-index: 1;
        }
      }
    }
  }
  &__carousel {
    max-height: 765px;
    @media(max-width: $xl){
    
    }
    @media(max-width: $lg){
    
    }
    @media(max-width: $md){
      max-height: none;
    }
    @media(max-width: $sm){
    
    }
    @media(max-width: $xs){
    
    }
    
    .swiper-wrapper {

    }
    .swiper-slide {

    }
    .content-row {
      margin-left: -80px;
      margin-right: -80px;
      overflow: visible;
      @media(max-width: $xl){
        margin-left: -40px;
        margin-right: -40px;
      }
      @media(max-width: $lg){
        margin-left: -20px;
        margin-right: -20px;
      }
      @media(max-width: $md){
        
      }
      @media(max-width: $sm){
      
      }
      @media(max-width: $xs){
      
      }
      
      & > * {
        padding-left: 80px;
        padding-right: 80px;
        @media(max-width: $xl){
          padding-left: 40px;
          padding-right: 40px;
        }
        @media(max-width: $lg){
          padding-left: 20px;
          padding-right: 20px;
        }
        @media(max-width: $md){
          padding-left: 45px;
          padding-right: 45px;
        }
        @media(max-width: $sm){
        
        }
        @media(max-width: $xs){
        
        }
        
      }
    }
    .content-row-el {
      position: relative;
      @media(max-width: $xl){
      
      }
      @media(max-width: $lg){
      
      }
      @media(max-width: $md){
        width: 100% !important;
        @include vertical-borders-single(-140px, calc(100% - 45px), 70px);
        padding-top: 0;
        padding-bottom: 0;
      }
      @media(max-width: $sm){
      
      }
      @media(max-width: $xs){
      
      }
      
      &:nth-of-type(1) {
        width: 30%;
        @include vertical-borders-single(0px, 0px, 0px);
        @media(max-width: $xl){
        
        }
        @media(max-width: $lg){
        
        }
        @media(max-width: $md){
          @include vertical-borders-single(0px, 100%, 0px);
          &::after {
            display: none;
          }
        }
        @media(max-width: $sm){
        
        }
        @media(max-width: $xs){
        
        }
        
      }
      &:nth-of-type(2) {
        width: 40%;
        @include vertical-borders-single(0px, 0px, -100px);
        @media(max-width: $md){
        &::after {
            display: none;
          }
        }
      }
      &:nth-of-type(3) {
        width: 30%;
        @media(max-width: $xl){
        
        }
        @media(max-width: $lg){
        
        }
        @media(max-width: $md){
          
        }
        @media(max-width: $sm){
        
        }
        @media(max-width: $xs){
        
        }
        
      }
    }
    .swiper-pagination {
      margin-bottom: 0;
      @media(min-width: $md){
        display: flex;
        flex-direction: column;
        position: absolute;
        left: 0;
        bottom: 0;
        top: auto;
        right: auto;
        transform: none;
        .swiper-pagination-bullet {
          &-active {
            height: 30px;
            width: 15px;
          }
        }
      }
    }
    .swiper__nav {
      position: static;
      display: none;
      @media(max-width: $xl){
      
      }
      @media(max-width: $lg){
      
      }
      @media(max-width: $md){
        display: flex;
      }
      @media(max-width: $sm){
      
      }
      @media(max-width: $xs){
      
      }
      
    }
    .swiper__nav-el {
      cursor: pointer;
      width: 60px;
      height: 60px;
      display: flex;
      background: $text url(../img/svg/arrow-right-white.svg) center no-repeat;
      background-size: 35px;
      border-radius: 100px;
      position: absolute;
      top: 350px;
      z-index: 1;
      @media(max-width: $xl){
      
      }
      @media(max-width: $lg){
      
      }
      @media(max-width: $md){
      }
      @media(max-width: $sm){
        top: 260px;
      
      }
      @media(max-width: $xs){
      
      }
      
      &--prev {
        left: 0;
      }
      &--next {
        right: 0;
      }
    }
  }
  &__slide-header {
    display: flex;
    flex-direction: column;
    gap: 50px;
    @media(max-width: $xl){
    
    }
    @media(max-width: $lg){
    
    }
    @media(max-width: $md){
      
    }
    @media(max-width: $sm){
      gap: 20px;
    }
    @media(max-width: $xs){
    
    }
    
  }
  &__slide-number {
    @extend .h1 !optional;

  }
  &__slide-title {
    @media(max-width: $xl){
    
    }
    @media(max-width: $lg){
      font-size: 40px;
    }
    @media(max-width: $md){
      
    }
    @media(max-width: $sm){
      font-size: 20px;
    }
    @media(max-width: $xs){
    
    }
    
  }
  .activity-image {
    gap: 70px;
    display: flex;
    flex-direction: column;
    @media(max-width: $xl){
    
    }
    @media(max-width: $lg){
    
    }
    @media(max-width: $md){
      gap: 30px;
    }
    @media(max-width: $sm){
    
    }
    @media(max-width: $xs){
    
    }
    
    &__img-container {
      border-radius: 75px;
      overflow: hidden;
      display: flex;
      max-height: 580px;
      @media(max-width: $xl){
      
      }
      @media(max-width: $lg){
      
      }
      @media(max-width: $md){
        height: 460px;
      }
      @media(max-width: $sm){
        height: 360px;
      }
      @media(max-width: $xs){
      
      }
      
    }
    &__img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
    &__links {
      display: flex;
      flex-wrap: wrap;
      gap: 10px 0;
      max-width: 380px;
      justify-content: space-between;
      @media(max-width: $xl){
      
      }
      @media(max-width: $lg){
      
      }
      @media(max-width: $md){
        margin-left: 20px;
      }
      @media(max-width: $sm){
      
      }
      @media(max-width: $xs){
      
      }
      
    }
    &__link {
      color: $text;
      display: flex;
      flex-direction: column;
      gap: 0px;
      justify-content: space-between;
      width: calc(50% - 15px);
      opacity: 0.6;
      &:hover {
        opacity: 1;
      }
      img {

      }
    }
    &__link-text {
      font-size: 12px;
    
    }
  }
  &__slide-description {
    height: 100%;
    display: flex;
    flex-direction: column;
    gap: 60px;
    justify-content: flex-end;
    
    @media(max-width: $xl){
    
    }
    @media(max-width: $lg){
    
    }
    @media(max-width: $md){
      gap: 30px;
      padding-left: 20px;
    }
    @media(max-width: $sm){
    
    }
    @media(max-width: $xs){
    
    }
    
    p {

    }
    .button {

    }
  }




  .decor {
    background-image: url(../img/svg/decor-activity.svg);
    
    
  }
}